:root {
  --main-family: "Epilogue", sans-serif;
  --trasform: 250ms cubic-bezier(.4, 0, .2, 1);
  --main-text-color: #fff;
  --main-button-color: #1e2827;
  --main-background-color: #141414;
}

.main-container {
  background-color: var(--main-background-color);
  font-family: var(--font-family);
  color: var(--main-text-color);
  width: 100%;
  padding: 40px 20px;
}

.main-container .main-section {
  padding-left: 20px;
  padding-right: 20px;
}

.main-container .main-section .welcome-title {
  width: 335px;
