  background-color: #1e2827;
  position: relative;
  overflow: hidden;
}

.main-container .main-section .carousel .slider .slide {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  padding: 5px 20px;
  transition: all 1s;
  display: flex;
  position: absolute;
}

.main-container .main-section .carousel .nav-carousel {
  flex-direction: row;
  justify-content: space-between;
  padding-top: 31px;
