}

.main-container .main-section .carousel .nav-carousel .btnslide-container {
  justify-content: center;
  align-items: center;
  gap: 12px;
  display: flex;
}

.main-container .main-section .carousel .nav-carousel .btnslide-container .btnslide {
  width: 32px;
  height: 32px;
  border: 1px solid rgba(255, 255, 255, .5);
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  padding: 7px;
  transition: border-color .25s ease-in-out;
  display: flex;
}

.main-container .main-section .carousel .nav-carousel .btnslide-container .btnslide:hover, .main-container .main-section .carousel .nav-carousel .btnslide-container .btnslide:focus {
